<!-- QuickPlan Main -->
<template>
  <el-container class="is-vertical">
    <AppHeader></AppHeader>
    <el-container>
      <AppSidebar></AppSidebar>
      <el-main>
        <!-- <keep-alive> -->
          <!-- <router-view/> -->
        <!-- </keep-alive> -->
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
import AppHeader from '@/components/AppHeader'
import AppSidebar from '@/components/AppSidebar'
export default {
  name: 'Main',
  components: {
    AppHeader,
    AppSidebar
  },
  watch: {
    $route () {
      this.setTitle()
    }
  },
  created () {
    this.setTitle()
  },
  methods: {
    // 當 route 切換時，將設定於 vue-route 中的標題寫入頁籤 title
    setTitle () {
      document.title = this.$route.meta.title ? `Quick Plan - ${this.$route.meta.title}` : 'Quick Plan'
    }
  }
}
</script>

